@import "variables";

.drink-grid {
    display: grid;
    grid-template-columns: repeat(3, 25%);
    justify-content: space-around;
    justify-items: center;
    grid-auto-flow: row;
    grid-row-gap: 5.5rem;

    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    padding-top: 13rem; //fit with header height (10) plus extra
    padding-bottom: 5.5rem;

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background: linear-gradient(to bottom right,
            rgba($color-primary, 0.075),
            rgba($color-primary, 0.01),
            transparent
        );

        z-index: -1;
    }

    .card {
        position: relative;
        width: 100%;
        height: 22rem;
        border-radius: 8px;
        border: 2px solid rgba($color-text, 0.6);
        overflow: hidden !important;

        box-shadow: -1px 2px 20px 1px rgba($color-primary-light, 0.4);
    }
}